import { Component, OnInit, ViewChild } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { LichSuMuonThietBiModel } from '../../../../../shared/models/lich-su-muon-thiet-bi.model';
import { MatTableDataSource } from '@angular/material/table';
import { MatSort } from '@angular/material/sort';
import { MatPaginator } from '@angular/material/paginator';
import { QuanLyNguoiHocService } from './lich-su-su-dung-thiet-bi.service';
import { environment, eMenuClient } from 'src/environments/environment';
import { MainNavComponent } from '../../main.component';
import { Router } from '@angular/router';
import { MatSnackBar } from '@angular/material/snack-bar';

@Component({
  selector: 'app-lich-su-su-dung-thiet-bi',
  templateUrl: './lich-su-su-dung-thiet-bi.component.html',
  styleUrls: ['./lich-su-su-dung-thiet-bi.component.scss'],
})
export class LichSuSuDungThietBiComponent implements OnInit {
  _length = 0;
  displayedColumns: string[] = [
    'Ma',
    'MaThietBiChiTiet',
    'TenThietBi',
    'TenNoiQuanLy',
    'MaSV',
    'HoTenSV',
    'NgaySinhSV',
    'SoDienThoaiSV',
    'EmailSV',
    'HoTenGV',
    'EmailGV',
    'NgayBatDau',
    'NgayKetThuc',
    'GhiChu',
    'IsHoanTat',
  ];

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  constructor(
    public dialog: MatDialog,
    private __service: QuanLyNguoiHocService,
    private __mainComponent: MainNavComponent,
    private __router: Router,
    private __snackbar: MatSnackBar
  ) {
    localStorage.setItem(
      environment.key_storage.MENU.CLIENT,
      eMenuClient[eMenuClient.LICH_SU_SU_DUNG_THIET_BI]
    );
    this.__mainComponent.__localStorage = localStorage.getItem(
      environment.key_storage.MENU.CLIENT
    );
  }
  _lichSuMuonThietBiModels: LichSuMuonThietBiModel[];
  dataSource: MatTableDataSource<LichSuMuonThietBiModel>;
  showProgressBar = false;
  ngOnInit(): void {
    this.showProgressBar = true;
    this.getDataThietBi();
  }
  delay(ms: number) {
    return new Promise((resolve) => setTimeout(resolve, ms));
  }

  private getDataThietBi() {
    this.showProgressBar = true;
    this.__service.getAll().subscribe(
      (data) => {
        this.showProgressBar = false;
        if(data.Message){
          this.__snackbar.open(data.Message, null, {
            duration: 2000,
          });
          return
        }
        this._lichSuMuonThietBiModels = data;
        this._lichSuMuonThietBiModels.forEach((f) => {
          f.NgayBatDauDate = new Date(f.NgayBatDau);
          f.NgayKetThucDate = new Date(f.NgayKetThuc);
        });
        this.dataSource = new MatTableDataSource(this._lichSuMuonThietBiModels);

        this._length = this.dataSource.data.length;
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
        // this.dataSource.filterPredicate =
        //   (data: LichSuMuonThietBiModel, filter: string) => (
        //     data.MaThietBi.toLocaleLowerCase().indexOf(filter) != -1
        //     || data.HoTenSV.toLocaleLowerCase().indexOf(filter) != -1
      },
      (error) => {
        this.showProgressBar = false;
        this.showLogin();
        this.__snackbar.open(environment.message.TOKEN_EXPIRED, null, {
          duration: 2000,
        });
      }
    );
  }
  showLogin() {
    this.__router.navigate(['login']);
  }
  applyFilter(filterValue: string) {
    filterValue = filterValue.trim(); // Remove whitespace
    filterValue = filterValue.toLowerCase(); // Datasource defaults to lowercase matches
    this.dataSource.filter = filterValue;
  }

  onSearchChange(searchText: String) {
    this.dataSource.filter = searchText.trim().toLowerCase();
  }
}
